:root {
  --primary: #80cbc4;
  --primary-variant: #4c968f; 
  --on-primary: #000000;

  --background: #121212;
  --on-background: #ffffff;

  --surface: rgba(255, 255, 255, 0.08);
  --on-surface: #ffffff;

  --error: #cf6679;
  --on-error: #000000;

  --on-disabled: #cccccc;
  --on-tag: #222;

  --on-footer: #999;

  --grid: #555;

  /** Tags */
  --visitor: #aaaaaa;
  --initiate: #eeeeee;
  --green: #46d160;
  --teal: #0aa18f;
  --purple: #7f00ff;
  --gold: #f5c234;
  --diamond: #b9f2ff;
  --ruby: #ff005d;
  --topaz: #ff6200;
  --jade: #307250;

  --twentyFour: #fcec3c;
  --mod: #fc3c46;
}

* {
  margin: 0;
  padding: 0;
}

a {
  text-decoration: none;
  color: var(--on-surface);
}

a:hover {
  text-decoration: underline;
  text-decoration-color: var(--on-surface);
}

html,
body {
  height: 100%;
}

body {
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  font-size: 14px;
  background-color: var(--background);
  color: var(--on-background);
  display: flex;
  flex-flow: column;
}

header, footer {
  flex-grow: 0;
}

#body-container {
  flex-grow: 1;
  width: 100%;
}

::placeholder {
  color: var(--on-surface);
  opacity: 0.2;
}

#body-container,
#header-content,
#footer-content {
  padding: 10px;
  margin: auto;
  max-width: 1100px;
}

#header-content {
  display: flex;
  flex-flow: row wrap;
  justify-content: space-between;
  align-items: center;
  height: 50px;
}

header {
  background-color: var(--surface);
  color: var(---primary);
  text-decoration-color: var(--primary);
}

#header-link {
  text-decoration-color: var(--primary);
}

footer {
  color: var(--on-footer);
}

#footer-content {
  display: flex;
  flex-flow: row;
  justify-content: space-between;
}

#footer-disclaimer {
  flex-basis: 70%;
  text-align: left;
}

#footer-copyright {
  text-align: right;
}

#footer-content a {
  color: var(--on-footer);
  text-decoration: underline;
}


#footer-content a:hover {
  color: var(--on-surface);
  text-decoration: underline;
}

h1,
h2,
h3 {
  color: var(--primary);
}

h1 {
  font-size: 32px;
}

h2 {
  font-size: 28px;
}

h3 {
  font-size: 24px;
}

#username {
  color: var(--on-background);
}

#user-header,
#tag-container {
  display: flex;
  flex-flow: row wrap;
  justify-content: flex-start;
  align-items: baseline;
  font-size: 14px;
}

#scribe-count {
  margin-left: 10px;
  font-size: 20px;
}

#search-container {
  margin: 50px 10px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 20px;
}

.user-input,
.search-button {
  padding: 5px 7px;
  border-radius: 3px;
}

#main-user-input, #main-search-button {
  font-size: 20px;
}

.user-input {
  background-color: var(--surface);
  color: var(--on-surface);
  border: solid 1px var(--primary);
}

.search-button {
  background-color: var(--primary-variant);
  color: var(--on-primary);
  border: solid 1px var(--on-primary);
}

.search-button:hover {
  background-color: var(--primary);
  cursor: pointer;
}

#progress-bar {
  width: 100%;
  height: 4px;
}

progress {
  background-color: var(--surface);
  border: none;
}

progress::-webkit-progress-bar {
  background-color: var(--surface);
}

progress::-moz-progress-bar {
  background-color: var(--primary);
}

progress::-webkit-progress-value {
  background-color: var(--primary);
}

#user-container {
  margin: 20px 0px;
  padding: 8px;
  background-color: var(--surface);
}

table {
  column-gap: 5px;
  border-collapse: collapse;
}

th, td {
  text-align: center;
  padding: 5px 7px;
}

.right-th {
  text-align: right;
}

.stat-container {
  display: flex;
  flex-flow: row wrap;
  justify-content: space-around;
}

/** Tags */

.tag {
  margin-left: 10px;
  padding: 3px 5px;
  border-radius: 6px;

  color: var(--on-tag);
}

.visitor {
  background-color: var(--visitor);
}
.initiate {
  background-color: var(--initiate);
}
.green {
  background-color: var(--green);
}
.teal {
  background-color: var(--teal);
}
.purple {
  background-color: var(--purple);
}
.gold {
  background-color: var(--gold);
}
.diamond {
  background-color: var(--diamond);
}
.ruby {
  background-color: var(--ruby);
}
.topaz {
  background-color: var(--topaz);
}
.jade {
  background-color: var(--jade);
}

.mod {
  background-color: var(--mod);
}

.twentyFour {
  background-color: var(--twentyFour);
}
